import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import { styles } from './styles';
import { NAVIGATION_LINKS } from '../../navigation/navigationLinks';
import { LinkButton } from '../../components';
import { closeModal, openModal, setContentUrl } from '../../store/reducers/modal';
import { connect } from 'react-redux';

HeaderMenu.propTypes = {};

const useStyles = makeStyles(styles);

function HeaderMenu(props) {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <div className={classes.headerTitleWrapper}>
        <LinkButton url={NAVIGATION_LINKS.HOME}>
          <span className={classes.headerTitle}>Andre Bozhenko</span>
        </LinkButton>
      </div>
      <div className={classes.navigationMenuWrapper}>
        <div className={classes.menuItemWrapper}>
          <LinkButton linkClass={classes.menuItem} url={NAVIGATION_LINKS.ABOUT}>
            About
          </LinkButton>
        </div>
        <div className={classes.menuItemWrapper}>
          <LinkButton linkClass={classes.menuItem} url={NAVIGATION_LINKS.ABOUT}>
            Dribbble
          </LinkButton>
        </div>
        <div className={classes.menuItemWrapper}>
          <LinkButton linkClass={classes.menuItem} url={NAVIGATION_LINKS.ABOUT}>
            Instagram
          </LinkButton>
        </div>
        <div className={classes.menuItemWrapper}>
          <LinkButton linkClass={classes.menuItem} url={NAVIGATION_LINKS.ABOUT}>
            Facebook
          </LinkButton>
        </div>
      </div>
    </div>
  );
}

const mapStateToProps = (state) => {
  return {
    modal: state.modal,
  };
};

const mapDispatchToProps = (dispatch) => {
  return {
    openModal: () => dispatch(openModal()),
    closeModal: () => dispatch(closeModal()),
    setContentUrl: (contentUrl) => dispatch(setContentUrl(contentUrl)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(HeaderMenu);
